.AddListBtn {
    background: #EBECF0;
    min-width: 250px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 40px;
    border-radius: 2px;
    padding-left: 5px;
    font-size: 14px;
    margin-left: 2em;
}

.AddListBtn:hover {
    background: #D9dce2;
    cursor: pointer;
}